.project-wrapper {
	border: 3px solid black;
	border-radius: 30px;
	height: 125px;
	width: 200px;
	padding: 3px;
	/* background-color: #1da1f2; */
	transition: 0.7s;
}
.project-wrapper:hover {
	/* opacity: 0.7; */
	background-color: #1da1f2;
}

.project-title {
	margin: 5px;
	font-size: 17px;
	color: black;
}

.project-title a,
.project-link {
	color: black;
	text-decoration: none;
	font-weight: 700;
}

.project-description {
	font-size: 12px;
	margin: 5px;
}
