.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	background-color: rgba(0, 0, 0, 0.5);
}
.popup_inner {
	position: absolute;
	left: 25%;
	right: 25%;
	top: 25%;
	bottom: 25%;
	font-size: 1.2vw;
	margin: auto;
	background: white;
	border: black solid 2px;
	border-radius: 20px;
}
.popup .close-btn {
	cursor: pointer;
	position: absolute;
	right: 25px;
	top: 20px;
	width: 30px;
	height: 30px;
	background: #222;
	color: #fff;
	font-size: 25px;
	font-weight: 600;
	line-height: 22px;
	text-align: center;
	border-radius: 50%;
	border: black 5px solid;
}
.popup-images {
	/* height: 70px; */
	max-width: 12%;
	overflow: hidden;
}

.popup-project-link {
	border: black solid 2px;
	border-radius: 20px;
	padding: 5px;
	text-decoration: none;
	color: black;
	transition: 0.7s;
	position: absolute;
	left: 40%;
	right: 40%;
	bottom: 10%;
	margin: auto;
	font-size: 100%;
}
.popup-project-link:hover {
	background-color: rgb(255, 251, 0);
}
.flex-popup-link {
	border: black solid 2px;
	border-radius: 20px;
	padding: 5px;
	text-decoration: none;
	color: black;
	transition: 0.7s;
	margin: auto;
	font-size: 100%;
}
.flex-popup-link:hover {
	background-color: rgb(255, 251, 0);
}
