/** @format */
@import 'Projects.css';
@import 'Popup.css';

html {
	scroll-behavior: smooth;
}

.App {
	text-align: center;
	font-family: Open Sans;
}

h2 {
	font-size: 24px;
}

.main-title {
	font-size: 36px;
}

.nav-container {
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: right;
	left: 0%;
	top: 0%;
	width: 100%;
	margin: 0px;
	padding: 0;
	height: 50px;
}

.nav-container a {
	font-weight: bold;
	text-decoration: none;
	color: black;
}

.nav-container a:hover {
	color: darkgrey;
}

.nav-container ul {
	display: flex;
	margin: 0;
	padding: 0;
}

.nav-container li {
	list-style: none;
	padding: 1em;
}

.flex-container {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: row;
	margin: 5px;
	flex-wrap: wrap;
}

.flex-element {
	margin: 5px;
}
.flex-popup {
	margin: 5px;
}
.flex-break {
	flex-basis: 100%;
	height: 0;
}

.aboutMe-wrapper {
	width: 75%;
	text-align: center;
	margin: auto;
	display: flex;
}

.aboutMe-paragraph {
	text-align: center;
	padding: 12px 0 0 0;
}

.photo-me {
	height: 75px;
	border-radius: 50%;
	text-align: center;
}

.main-title {
	margin: 5px;
	font-weight: 900;
	font-size: 54px;
}

.icon-image {
	height: 20px;
	opacity: 0.7;
	transition: 0.3s;
}

.icon-image:hover {
	opacity: 1;
}

.skill-image {
	height: 100px;
}
